<template>
  <div id="trash" class="detail">
    <div class="note-sidebar">
      <h3 class="notebook-title">回收站</h3>
      <div class="menu">
        <div>更新时间</div>
        <div>标题</div>
      </div>
      <ul class="notes">
        <li v-for="noteItem in trashNoteItems">
          <router-link :to="`/trash?noteItemId=${noteItem.id}`">
            <span class="date">{{ noteItem.friendlyUpdatedAt }}</span>
            <span class="title">{{ noteItem.title }}</span>
          </router-link>
        </li>
      </ul>
    </div>

    <div class="note-detail">
      <div class="note-bar" v-if="true">
        <span> 创建日期: {{ curTrashNoteItem.friendlyCreatedAt }}</span>
        <span> | </span>
        <span> 更新日期: {{ curTrashNoteItem.friendlyUpdatedAt }}</span>
        <span> | </span>
        <span> 所属笔记本: {{ belongTo }}</span>

        <a class="btn action" @click="onRevert">恢复</a>
        <a class="btn action" @click="onDelete">彻底删除</a>
      </div>
      <div class="note-title">
        <span>{{ curTrashNoteItem.title }}</span>
      </div>
      <div class="editor">
        <div class="preview markdown-body" v-html="compiledMarkdown"></div>
      </div>
    </div>
  </div>
</template>

<script>
import markdownIt from "markdown-it";
import { mapGetters, mapMutations, mapActions } from "vuex";

let md = new markdownIt();

export default {
  created() {
    this.checkLogin();
    this.getNoteList();
    this.getAllTrashNoteItems().then(() => {
      this.setCurTrashNoteItem({
        curTrashNoteItemId: this.$route.query.noteItemId
      });
      this.$router.replace({
        path: `/trash?noteItemId=${this.curTrashNoteItem.id}`
      });
    });
  },

  data() {
    return {};
  },

  computed: {
    compiledMarkdown() {
      return md.render(this.curTrashNoteItem.content || "");
    },

    ...mapGetters(["trashNoteItems", "curTrashNoteItem", "belongTo"])
  },

  methods: {
    ...mapMutations(["setCurTrashNoteItem"]),

    ...mapActions([
      "checkLogin",
      "getAllTrashNoteItems",
      "deleteTrashNoteItem",
      "revertTrashNoteItem",
      "getNoteList"
    ]),

    onRevert() {
      this.revertTrashNoteItem({
        noteItemId: this.curTrashNoteItem.id
      }).then(() => {
        this.setCurTrashNoteItem();
        this.$router.replace({
          path: `/trash?noteItemId=${this.curTrashNoteItem.id}`
        });
      });
    },

    onDelete() {
      this.$confirm("删除后将无法恢复", "确定删除？", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          return this.deleteTrashNoteItem({
            noteItemId: this.curTrashNoteItem.id
          });
        })
        .then(() => {
          this.setCurTrashNoteItem();
          this.$router.replace({
            path: `/trash?noteItemId=${this.curTrashNoteItem.id}`
          });
        });
    }
  },

  beforeRouteUpdate(to, from, next) {
    this.setCurTrashNoteItem({ curTrashNoteItemId: to.query.noteItemId });
    next();
  }
};
</script>

<style lang="less">
@import url(../assets/css/note-sidebar.less);
@import url(../assets/css/note-detail.less);

#trash {
  display: flex;
  align-items: stretch;
  background-color: #fff;
  flex: 1;

  .note-bar {
    .action {
      float: right;
      margin-left: 10px;
      padding: 2px 4px;
      font-size: 12px;
    }
  }
}
</style>
